.SectionDetail__Main {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 1rem;
  padding: 16px;
}

.SectionDetail__Item {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.SectionDetail__Item__Image {
  min-height: 200px;
  max-height: 300px;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1rem;
}

.SectionDetail__Item__Image__Title {
    font-size: 1.5em;
    padding: 1rem;
    color: white;
    background: rgba(0, 0, 0, 0.5);
}
