.Home {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(min(300px, 100%), 1fr));
  padding: 1rem;
  gap: 1rem;
}

.SelectionCard {
  height: 210px;
  border-radius: 1rem;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.SelectionCard__TitleSection {
  padding: 8px;
  background: rgba(0, 0, 0, 0.5);
  height: 50px;
  border-radius: 0 0 1rem 1rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.SelectionCard__TitleSection span {
  font-size: 2em;
  padding-left: 8px;
  display: inline-flex;
  align-items: center;
  color: white;
}

.SelectionCard__TitleSection img {
  font-size: medium;
  width: 50px;
  color: white;
  fill: white;
}
